.page {
  min-height: 100vh;
  max-width: 1280px;
  min-width: 300px;
  background: #000000;
  margin: 0 auto;
  padding: 0 50px;
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  box-sizing: border-box;
}

@media screen and (max-width: 740px) {
  .page {
    padding: 0 32px;
  }
}

@media screen and (max-width: 556px) {
  .page {
    padding: 0;
  }
}
